.switch-btn {
  display: inline-block;
  width: 75px;
  /* ширина */
  height: 38px;
  /* высота */
  border-radius: 19px;
  /* радиус скругления */
  background: #FE043C;
  /* цвет фона */
  z-index: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  position: relative;
  transition-duration: 200ms;
  /* анимация */
  border: 2px solid #fff; }
  .switch-btn:hover {
    box-shadow: 1px 1px 11px 4px #D7D7D7; }

.switch-btn::after {
  content: "";
  height: 32px;
  /* высота кнопки */
  width: 32px;
  /* ширина кнопки */
  border-radius: 17px;
  background: #fff;
  /* цвет кнопки */
  top: 50%;
  /* положение кнопки по вертикали относительно основы */
  left: 2px;
  /* положение кнопки по горизонтали относительно основы */
  transition-duration: 200ms;
  /* анимация */
  position: absolute;
  z-index: 1;
  transform: translateY(-50%); }

.switch-on {
  background: #35CD8C; }

.switch-on::after {
  left: 37px; }

